import React from "react";
import "./SeriesSection.css";
import SeriesInfo from "../SeriesInfo/SeriesInfo";
const SeriesSection = (props) => {
  const imagePath = "/RPI_Lab2/img/" + props.imagePath;
  return (
    <div className="SeriesSection">
      <img src={imagePath} alt={"Series " + props.imagePath} className="imgOfSeries" />
      <SeriesInfo
        name={props.name}//""
        year={props.year}//"1989"
        country={props.country  }//""
        genre={props.genre}//", "
        writers={props.writers}//" . ,  ,  "
        seasons={props.seasons}//"33"
        time={props.time}//"22 " //"    ,  ,     ,          .      ,     ,      ,       ."
        article={props.article}/>
    </div>
  );
};

export default SeriesSection;